body {
  background-image: url("/assets/images/page-background.jpg");
  padding-top: 50px;
  padding-left: 10px;
  font-family: "Buenard";
  font-size: 13px;
  padding-right: 20px; 
}

::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}

a {
    color: #23527c !important;
    text-decoration: underline;
  }

a:focus, a:hover {
  color: white;
}

iframe {
  border: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  padding-top: 10px;
}

h1, h2, h3, h4, h5 {
    margin-top: .2em;
    margin-bottom: .2em;
    font-family: MrJeeves;
    font-weight: 900;
    color: #58180D;
    font-variant: small-caps;
  }

p.phb::first-letter {
  float: left;
  font-family: serif;
  font-size: 2em;
  font-weight: 500;
  color: #222;
  line-height: .4em;
}    

table {
  font-family: ScalySans;
  width: 100%;
  font-size: 10pt;
  border-collapse: collapse;
  border-spacing: 0;
}

.classTable {
  line-height: 1.4;
  margin-top: 25px;
  margin-bottom: 40px;
  border-collapse: separate;
  background-color: white;
  border: initial;
  border-style: solid;
  border-image-outset: 25px 5px;
  border-image-repeat: stretch;
  border-image-slice: 150 200 150 200;
  border-image-source: url(/assets/images/table.png);
  border-image-width: 47px;
}

.classTable thead th {
    vertical-align: bottom;
    padding-bottom: .3em;
    padding-right: .1em;
    padding-left: .1em;
}

.classTable tr:nth-child(odd) td {background-color: #b5e2f1;}  

.dropdown-toggle {
  color: floralwhite !important;
}

.featureTable {
  width: 50%;
}

.menuDivider {
  background: #d42129;
  border: 0;
  color: white;
  display: flex;
  padding: 2px 5px 2px 5px;
  cursor: pointer;
}

.menuHeader {
  cursor: pointer;
  padding: 5px;
  background: #d42129;
  border: 0;
  color: white;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.last {
  border-bottom-left-radius: 10px;
}

.leftCol {
/*  border-right: 5px solid transparent;
  padding: 15px;
  border-image: url(flagbanner.png) 30% repeat;
  border-image-repeat: repeat;*/
  border-right: 1px solid #ccc;
}

.subMenu {
  cursor: pointer;
  display: flex;
  padding: 2px 5px 2px 5px;
  background: #ED1B24;
  border: 0;
  color: white;
}

.subMenu:hover {
  text-decoration: underline;
}

#logo {
  display: inline-block;
  margin-left: -30px;
  margin-top: -30px;
  margin-bottom: -50px;
  width: 300px;
  height: 50px;
}

#main {
  height: 297mm;
}

#nextLevel {
  padding: 5px;
  background-color: #b5e2f1;
  border: 1px solid;
}

/* Tooltip */
.tooltip-inner {
  max-width: 350px;
  width: 350px; 
  text-align: left !important;
}
.tooltip-header {
  color:antiquewhite;
}
/* End Tooltip */

/* ---------------------------------------------------------------------------------------  */    
/* This entire section is to make navbar not wrapping into two rows on certain screen sizes */
@media (max-width: 1000px) {
  .navbar-header {
      float: none;
  }
  .navbar-left,.navbar-right {
      float: none !important;
  }
  .navbar-toggle {
      display: block;
  }
  .navbar-collapse {
      border-top: 1px solid transparent;
      box-shadow: inset 0 1px 0 rgba(255,255,255,0.1);
  }
  .navbar-fixed-top {
      top: 0;
      border-width: 0 0 1px;
  }
  .navbar-collapse.collapse {
      display: none!important;
  }
  .navbar-nav {
      float: none!important;
      margin-top: 7.5px;
  }
  .navbar-nav>li {
      float: none;
  }
  .navbar-nav>li>a {
      padding-top: 10px;
      padding-bottom: 10px;
  }
  .collapse.in{
      display:block !important;
  }
}
/* End navbar section  */
